<template>
  <div class="mind-card">
    <div class="mind-card__number">
      <span>{{ questionLeveLIndex + 1 }}</span>
      <span>{{ questionData.length }}</span>
    </div>
    <div class="mind-card__question">
      {{ questionData[questionLeveLIndex].questions[subQuestionLevel].question }}
    </div>
    <div class="mind-card__answers">
      <button class="mind-card__btn" @click="nextQuestion(questionData[questionLeveLIndex].questions[subQuestionLevel].answers[0].result)">
        {{ questionData[questionLeveLIndex].questions[subQuestionLevel].answers[0].text }}
      </button>
      <button class="mind-card__btn" @click="nextQuestion(questionData[questionLeveLIndex].questions[subQuestionLevel].answers[1].result)">
        {{ questionData[questionLeveLIndex].questions[subQuestionLevel].answers[1].text }}
      </button>
    </div>
    
  </div>
</template>

<script>
export default {
  props: {
    questionData: {
      type: Object,
      required: true
    },
    questionLeveLIndex: {
      type: Number,
      required: true
    },
    subQuestionLevel: {
      type: Number,
      required: true
    },
    nextQuestion: {
      type: Function,
      required: true
    }
  }
}
</script>

<style lang="scss">
  @import "../assets/scss/base";  
  @import '../assets/scss/mind-card.scss';
</style>